// Vars & fonts

$black: rgb(3, 3, 3);
$white: #f7f7f7;

@font-face {
    font-family: pixel;
    src: url('../app/fonts/Pixel_Square_10.ttf');
  }

// Universal styles

* {
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    line-height: 1.5;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

*, *:before, *:after {
    box-sizing: inherits;
}

html {

    scroll-behavior: smooth;
}

html, body {
    margin: 0;
    height: 100%;
}

body {
    width: 100%;
    background-color: $black;
    background-image: url("../app/images/2021NGSite_Bg_Blur.png");
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    background-attachment: fixed;
}

h1, h2, h3, h4, p, ul, ol {
    color: $white;
}

ul, ol {
    padding-left: 2rem;
}

a {
    color: #FF0045;
    text-decoration: none;
}

h1 {
    font-family: pixel 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    margin-top: 1rem;
    margin-bottom: 1rem;
    font-size: 3vw;
}

#header-red, #the-master-plan {
    font-size: 2.5vw;
    color:#FF0045;
}

#header-yellow {
    font-weight: 700;
    color: #FFF80B;
}

h1 a {
    font-family: pixel, 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

h2 {
    font-size: 2vw;
}

h2, #a-txt {
    font-family: pixel 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    margin-top: 1rem;
    margin-bottom: 1rem;
    
    font-weight: 500;
}

#a-txt {
    font-size: 1.5vw;
}

h3 {
    font-family: pixel 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-size: 1.5vw;
    font-weight: 500;
}

p, ul, ol {
    font-size: 1vw;
}

// Home Page

#vert-text {
    font-size: 2vw;
    grid-column: 1 / 1;
    grid-row: 1 / 1;
    z-index: -1;
    transform: rotate(-90deg) translateY(-40%) translateX(18.3%);
}

.container {
    width: 95%;
    height: 100%;
    margin: auto;
    padding-top: 1.5rem;
    // margin-bottom: 1rem;

}

.inner-container{
    height: 100%;
    // width: 75%; // changed on 06/09/21
    width: 80%;
    // width: 100%;
    margin: auto;
}

.big-img-wrapper, .med-img-wrapper {

    display: grid;
    grid-template-columns: repeat(1, 1fr);
    grid-template-rows: repeat(1, 1fr);
    padding-bottom: 2rem;

    // box-shadow: rgb(255, 0, 43) 6px -6px;
}

.big-img-wrapper img, .med-img-wrapper img {
    grid-column: 1 / 1;
    grid-row: 1 / 1;
    margin: auto;
}

.big-img-wrapper img {
    max-width: 100%;

}

.med-img-wrapper img {
    max-width: 60%;
}

.big-img-wrapper svg {
    grid-column: 1 / 1;
    grid-row: 1 / 1;
    max-width: 100%;
    

}

.big-img-wrapper .page-items {
    grid-column: 1 / 1;
    grid-row: 1 / 1;
}

.page-items-inner {
    height: 100%;
    padding: 0;
    margin: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.page-items-inner h1 {
    font-family: pixel 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-size: 4vw;
}

.svg-container, .svg-container-small {
    display: flex;
}

.svg-container {
    width: 100%;
    height: 100%;
    background-color: black;
    top:50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
    position: absolute;
}

.svg-container.active {
    
    animation: fadeAnimation 1s;
    animation-fill-mode: forwards;
}

@keyframes fadeAnimation {
    
    0% {
        opacity:1;
        visibility: visible;
    }
    70% {
        opacity:1;
      }
      100% {
        opacity:0;
        visibility: hidden;
      }
    
}

.svg-container-small {
    width: 70%;
    margin: auto;
}

.svg-container svg, .svg-container-small svg {
    display: block;
    margin: auto;
}

.svg-container svg {
    height: 75%;
    
}



// 
text {
    font-family: pixel, 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-size: 2rem;
}

tspan a {
    color: #FF0045;
}

#glitch-logo.active {
    animation: flickerAnimation .6s 1;
}

#scroll-up-container.hidden {
    display: none;
}

@keyframes flickerAnimation {
    0%   { opacity:0; }
    7%   { opacity:0; }

    8%   { opacity:1; }
    15%  { opacity:1; }

    16%  { opacity:0; }
    23%  {opacity: 0; }

    24%  { opacity:1; }
    31%  { opacity:1; }

    32%  { opacity:0; }
    62%  { opacity:0; }

    63%  { opacity:1; }
    100%  { opacity:1; }

    // 72%  { opacity:0; }
    // 90%  { opacity:0; }

    // 91%  { opacity:1; }
    // 100% { opacity:1; }
}

#switch-off {
    cursor: pointer;
}

#guts {
    display: none;
}

