header img {
    position: fixed;
    top: 2rem;
    left: 1rem;
    max-width: 7%;
}

#back-arrow:hover {
    transform: scale(1.05);
}

#logo {
    max-width: 10%;
    position: fixed;
    top: 2rem;
    left: 1%;
}

#tagline {
    position: fixed;
    margin: 0;
    max-width: 10%;
    bottom: 3%;
    right: 0%;
}

/* Sets the containers height and width */
.base-timer {
    position: relative;
    height: 300px;
    width: 300px;
  }
  
  /* Removes SVG styling that would hide the time label */
  .base-timer__circle {
    fill: none;
    stroke: none;
  }
  
  /* The SVG path that displays the timer's progress */
  .base-timer__path-elapsed {
    stroke-width: 7px;
    stroke: grey;
  }

  #explo-wrapper {
    margin: auto;
    display: flex;
    justify-content: center;
  }

  #explo {
      width: 30%;
  }

  #explo.active {
      overflow: hidden;
      animation: blowUp 1s 1;
      transform: scale(3.5);
  }

  @keyframes blowUp {
    0%   { transform: scale(1); }
    20%  { transform: scale(3.5); }
    100% { transform: scale(3.5); }
    
}

// Menu

#menu-ul {
    max-width: 8%;
    position: fixed;
    margin-top: 2rem;
    margin-left: 1.5rem;
}

#menu-li, #menu-li0 {
    margin-bottom: 1vw;
    list-style: none;
}

#menu-ul #menu-li a, #menu-li, #menu-li0 a {
    font-family: pixel, 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    color: white;
    font-size: 1.5vw;
    line-height: 1;
}

#menu-ul #menu-li a:hover {
    color: grey;
}

#menu-ul #menu-li:nth-child(2){
    margin-top: 3rem;
}

#grey-on-hover:hover {
    color: grey;
}

#go-back:hover {
    background-color: #df003b;
}

#go-back {
    background-color: #FF0045; /* Green */
    border: none;
    border-radius: 15px;
    color: white;
    padding: 1.25vw 1.75vw;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 2vw;
}

// 

footer {
    position: fixed;
    width: 10%;
    display: flex;
    flex-wrap: wrap;
    justify-content: left;
    bottom: 1%;
    left: 1.5%;
    padding: 5px;
    border-radius: 10px;
    background-color: transparent;


}
footer a {
    margin: 0.5rem;
    color: white;
    width: 30px;
    display: flex;
    justify-content: left;
    
}

footer a:nth-last-child() {
    margin-top: 2rem;
    
}

footer a:hover {
    color: grey;
}

#a-txt {
    width: 100%;
    margin: 0.25rem;
}

#a-txt:hover {
    color: grey;
}

footer a:nth-child(7) {
    padding-top: 1rem;    
}

.lower-content-outer{
    background-color: rgba($color: #0f0f0f, $alpha: 0.7);
    border-radius: 15px;
    padding-left: 7rem;
    padding-right: 7rem;
    padding-top: 2rem;
    padding-bottom: 2rem;
}

.buffer {
    height: 4rem;
}


#subpage-banner > svg {
    border-radius: 15px;
}

#scroll-arrow {
    max-width: 5vw;
}

// 

.story-block {
    display: flex;
}

.story-block p {
    width: 90%;
}

.face-wrapper {
    width: 10%;
}

.face-wrapper img {
    max-width: 80%;
    margin-right: 1rem;
}


#master-plan {
    max-width: 80%;
    display: block;
    margin: auto;
}

// DONATE PAGE //

#donate-page {
    
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    
}

#donate-page-sub {
    max-width: 100%;
    margin: 2rem;
    display: flex;
    flex-direction: row;
    justify-content:center;
    justify-self: center;
}

.playlist-wrapper {
    position: relative;
    overflow: hidden;
    width: 100%;
    padding-top: 56.25%; /* 16:9 Aspect Ratio (divide 9 by 16 = 0.5625) */
}

.responsive-iframe {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    width: 100%;
    height: 100%;
}

// M3D14 QU3R13S //

@media all and (min-width: 2214px) {
    footer {
        font-size: 1vw;
        width: 8%;
    }
    footer a{
        margin: 1vw;
    }
}

@media all and (max-width: 1024px) {

    footer {
        font-size: 1.7vw;
        width: 12%;
    }

    footer a{
        margin-left: 0;
        margin-right: 0;
    }

    .lower-content-outer {
        padding: 1rem 2rem;
    }

    p, ul, ol {
        font-size: 2vw;
    }

    .med-img-wrapper img {
        max-width: 80%;
    }

    #menu-ul {
        margin-top: 2rem;
        margin-left: -0.5rem;
    }
    
    #menu-ul #menu-li a, #menu-li, #menu-li0 a {
        font-size: 1.5vw;
    }
    
    #menu-ul #menu-li:nth-child(2){
        margin-top: 2rem;
    }

    #go-back {
        padding: 1.5vw 3vw;
    }

}

@media all and (max-width: 768px) {

    h2 {
        font-size: 3vw;
    }

}

@media all and (max-width: 568px) {

    #header-red, #the-master-plan {
        font-size: 3vw;
    }
    
    p, ul, ol {
        font-size: 3vw;
    }

    #menu-ul {
        max-width: 5%;
        position: fixed;
        margin-top: 2rem;
        margin-left: -1.5rem;
    }
    
    #menu-ul #menu-li a, #menu-li, #menu-li0 a {
        font-size: 2vw;
        margin-bottom: 0.5rem;
    }
    
    #menu-ul #menu-li:nth-child(2){
        margin-top: 0rem;
    }

    #go-back {
        padding: 2vw 5vw;
    }


    
}